export default {
	data() {
		return {
			login: false,
			avatarUrl: "../../static/logo.png",
			uerInfo: {
				name:"**",
				avatarUrl:""
			}
		}
	},
	onLoad() {
		this.login=this.$cache.get('_loginFlag')
		if(this.$cache.get('_user') != null){
			this.uerInfo.name=this.$cache.get('_user').username
			this.uerInfo.avatarUrl=this.$cache.get('_user').avatar
		}
	},
	methods: {
		goLogin() {
			if (!this.login) {
				console.log("点击前往登录")
			}
		},
		toinfo() {
			this.$openPage('info')
		}
	}
}
